@import "normalize";
@import "vars";

//  Blocks

@import "blocks/animation.scss";
@import "blocks/main-nav.scss";
@import "blocks/logo.scss";
@import "blocks/site-list.scss";
@import "blocks/intro.scss";
@import "blocks/bitcard__list.scss";
@import "blocks/buttons.scss";
@import "blocks/arrow-turn__icon.scss";
@import "blocks/shape-stars.scss";
@import "blocks/load-up.scss";
@import "blocks/flash-hemisphers__image.scss";

// Vendor libs

@import "vendor/slick.scss";

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  min-height: 100vh;

  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #000;
  line-height: inherit;

  box-sizing: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;

  @media (min-width: $breakpoint_lg) {
    padding: 0 30px;
    max-width: 1200px;
  }
}
