.load-up__title {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  font-size: 27px;
  font-weight: 500;
  line-height: 1;
  text-align: center;
}

.load-up__text {
  margin: 0;
  margin-bottom: 26px;
  padding: 0 10px;

  text-align: center;
  line-height: 22px;
  color: #d4d4d4;
}

.load-up-item__wrapper {
  margin-bottom: 40px;
}

.load-up__item {
  margin: 0;
  padding: 0;

  & + & {
    margin-top: 17px;
  }
}

.load-up__item label {
  display: block;
  margin-bottom: 4px;
  padding: 0 15px;

  font-size: 12px;
  color: #ed9c00;
}

.load-up__item input {
  margin: 0;
  padding: 12px 14px 13px;
  width: 100%;

  font-size: 16px;
  color: #333333;

  border: 1px solid #d1d1d1;

  &::placeholder {
    color: #999999;
  }

  &::-moz-placeholder {
    color: #999999;
  }

  &::-webkit-input-placeholder {
    color: #999999;
  }

  &:focus,
  &:hover,
  &:active {
    border-color: #ed9c00;
    outline: #ed9c00;
  }
}
