@keyframes flashing {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes flashing-arrow {
  0% {
    opacity: 1;
    transform: translate(-50%, 50px);
  }
  10% {
    opacity: 1;
    transform: translate(-50%, 50px);
  }

  25% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  30% {
    opacity: 0;
    transform: translate(-50%, 0px);
  }
  35% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  40% {
    opacity: 0;
    transform: translate(-50%, 0px);
  }
  45% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  50% {
    opacity: 0;
    transform: translate(-50%, 0px);
  }
  55% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  75% {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 50px);
  }
}

@keyframes increase {
  from {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
