.button {
  display: block;

  font: inherit;
  line-height: 1;

  border: none;
  border-radius: 6px;
  outline: none;

  cursor: pointer;
}

.button__use-code,
.button__transfer {
  margin: 0 auto;
  padding: 16px;
  min-width: 260px;

  color: #fff;
  font-weight: 500;

  background: linear-gradient(to right, #ed9c00, #ffc600);

  @media (min-width: $breakpoint_lg) {
    min-width: 210px;
  }
}

.button__transfer {
  margin-bottom: 37px;
}
