.site-list__wrapper {
  @media (max-width: $breakpoint_lg) {
    .main-nav--closed & {
      display: none;
    }

    .main-nav--no-js & {
      position: relative;

      display: block;
    }

    .main-nav--opened & {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;

      display: block;
      width: 100%;

      background-color: #fff;
    }
  }
}

.site-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  margin-left: 40px;

  list-style: none;

  @media (max-width: $breakpoint_lg) {
    display: block;
    width: 100%;
    margin: 0;
    padding: 55px 0 80px;
  }
}

.site-list__item {
  padding: 10px 20px 10px 14px;
  margin-left: 6px;

  font-size: 16px;
  line-height: 1;
  color: #ffffff;

  &:hover,
  &:focus,
  &:active {
    border-left: 3px solid #fabb00;

    .site-list__link {
      margin-left: -3px;

      color: #fabb00;
    }
  }

  @media (max-width: $breakpoint_lg) {
    font-size: 27px;
    line-height: 60px;
    margin: 0 37px;
    padding: 0;
    padding-left: 20px;

    .main-nav--closed & {
      display: none;
    }

    .main-nav--no-js & {
      display: block;
      background-color: #fff;
      list-style: none;
    }

    &:hover,
    &:focus,
    &:active {
      .site-list__link {
        color: #000;
      }
    }
  }
}

.site-list__link {
  text-decoration: none;
  color: #ffffff;

  @media (max-width: $breakpoint_lg) {
    color: #000;
  }
}

.button-close {
  position: absolute;
  top: 10px;
  right: 20px;

  width: 20px;
  height: 20px;

  background-color: #fff;
  border: none;
  outline: none;

  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;

    width: 20px;
    height: 4px;
    background-color: #000;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(135deg);
  }

  .main-nav--no-js & {
    display: none;
  }

  @media (min-width: $breakpoint_lg) {
    display: none;
  }
}
