.flash-hemisphers {
  position: relative;

  display: block;
}

.flash-hemisphers__image {
  display: block;
  margin: 0 auto;
  cursor: pointer;

  animation: increase 2s linear alternate;
  animation-iteration-count: infinite;
  transform-origin: 50% 100%;

  &:hover,
  &:active {
    animation-play-state: paused;
  }

  &:hover + .arrow-down__image,
  &:active + .arrow-down__image {
    animation-play-state: paused;
  }

  @media (max-width: "360px") {
    // margin: 0 -5.5%;
    width: 100%;
  }
}

.arrow-down__image {
  position: absolute;
  left: 50%;
  top: 80%;
  transform: translateX(-50%);

  animation: flashing-arrow 4s infinite;
}

.arrow-down__image use {
  fill: #fff;
}
