.bitcard__list {
  margin: 0;
  padding: 0;

  list-style: none;

  @media (min-width: $breakpoint_md) {
    margin-bottom: 40px;
  }

  @media (min-width: $breakpoint_lg) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.bitcard__item {
  margin-bottom: 40px;
  padding: 0;
  padding-top: 15px;

  text-align: center;

  outline: none;

  @media (min-width: $breakpoint_md) {
    margin-bottom: 0;
  }

  @media (max-width: 345px) {
    padding-right: 15px;
  }

  @media (min-width: $breakpoint_lg) {
    & + & {
      margin-left: 30px;
    }
  }
}

.bitcard__wrapper {
  position: relative;

  width: 260px;
  height: 162px;
  margin: 0 auto;
  margin-bottom: 26px;
  padding: 0 15px;

  perspective: 1000px;

  @media (min-width: $breakpoint_md) {
    width: 324px;
    height: 220px;
  }

  @media (min-width: $breakpoint_lg) {
    padding: 0;
    width: 342px;
    height: 225px;
  }
}

.bitcard__turn {
  position: absolute;
  top: -15px;
  right: -15px;
  z-index: 20;

  outline: none;

  cursor: pointer;

  &:hover + .bitcard__image,
  &:focus + .bitcard__image {
    transform: rotateY(180deg);
  }

  &:hover ~ .bitcard__image--reverse,
  &:focus ~ .bitcard__image--reverse {
    transform: rotateY(360deg);
  }
}

.bitcard__image,
.bitcard__image--reverse {
  position: absolute;
  top: 0;
  left: 0;

  width: 260px;
  height: 162px;

  backface-visibility: hidden;
  transition: transform 0.6s linear;
  perspective: 700px;

  @media (min-width: $breakpoint_md) {
    width: 324px;
    height: 220px;
  }

  @media (min-width: $breakpoint_lg) {
    width: 342px;
    height: 225px;
  }
}

.bitcard__image {
  z-index: 5;

  transform: rotate(0deg);

  &--reverse {
    transform: rotateY(180deg);
  }
}

.bitcard__price {
  margin: 0;
  padding: 0;

  font-weight: 500;
  color: #f6b100;

  & span {
    color: #fff;
  }
}
