.intro {
  padding-bottom: 55px;

  background-image: url("../img/background_top_mobile.png");
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;

  @media (min-width: $breakpoint_md) {
    background-image: url("../img/background_top_desktop.png");
  }
}

.site-title {
  margin: 0;
  margin-bottom: 40px;
  padding: 45px 35px 0;

  font-size: 27px;
  font-weight: 400;
  line-height: 38px;
  color: #fff;
  text-align: center;

  @media (min-width: $breakpoint_lg) {
    font-size: 48px;
    line-height: 48px;
    font-weight: 500;
  }
}
