.main-nav {
  .main-nav--opened & {
    position: relative;
  }
}

.main-nav__wrapper {
  background-color: #00112d;
}

.main-nav__columns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-height: 40px;

  @media (min-width: $breakpoint_lg) {
    min-height: 70px;
  }
}

.main-nav__toggle {
  @media (max-width: $breakpoint_lg) {
    padding: 0;

    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    .main-nav--closed & {
      display: block;
    }

    .main-nav--no-js &,
    .main-nav--opened & {
      display: none;
    }
  }

  display: none;
}
